import localStorageUtility from './localStorageUtility';
export { localStorageUtility };
export const counter = (a: string[]): Typing.IKeyValue<string, number>[] => {
    const obj = {};
    for (let i = 0; i < a.length; i++) {
        obj[a[i]] = 1 + (obj[a[i]] || 0);
    }

    const arr: Typing.IKeyValue<string, number>[] = [];
    for (let key in obj) {
        arr.push({ key, value: obj[key] });
    }

    return arr;
};

export const removeEmptyOrSlash = (value: string): string => {
    const result = value.replace(/( |\/)/g, '');
    return result;
};

export const resizeTarget = (target: Typing.IReSizeTarget): string => {
    const targetValue: Typing.IDictionary<Typing.IReSizeTarget, string> = {
        facebook: `52.356%`,
        youtube: `57%`,
    };
    return targetValue[target];
};
